import { decryption } from "../Encryption/decrypt";
import { DELETE, PATCH, POST } from "../UrlConfig/MethodType";

// all of encrypted api only==========
export const payrollActivateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v2/payroll/ins/${data.instituteId}/payroll/query`,
      method: POST,
      body: data.bodyParameter,
    }),
  });
};

export const payrollUpdateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v2/payroll/payroll/staff/${data.osid}?nsid=${data.nsid}`,
      method: PATCH,
    }),
    invalidatesTags: ["Payroll_Module"],
  });
};

export const payrollDashboardMasterFunction = (builder) => {
  return builder.query({
    query: (data) => `/v2/payroll/${data.pid}/dashboard?mod_id=${data?.mid}`,
    transformResponse: (response) => {
      let dt = decryption(response?.encrypt);
      return dt?.payroll;
    },
    providesTags: ["Payroll_Module"],
  });
};

